.skills-container,
.techContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.5cm;
  row-gap: 0.5cm;
  padding: 0.5cm;
  margin: 3px;
  flex-wrap: wrap;

}

.skill,
.technology {
  border: thin solid antiquewhite;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 12px;
  padding: 5px;
  height: 2.5cm;
  width: 2.5cm;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.2cm;
  border-radius: 15px;
}

.skill:hover, .technology:hover{
  transform: scale(1.1);
  transition: 0.5s;

}
.skillImg,
.techImg {
  height: 1.5cm;
  width: 1.5cm;
  
}

