* {
    margin: 0;
    padding: 0;
    background-color: rgb(255, 255, 255);
}

.Home {
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.Body {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width: 100%;
}

.intro {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm 1cm;
    column-gap: 3.5cm;
    margin: 0.5cm;
}

.para {
    font-size: 14px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    /* padding: 0.5px; */
    display: flex;
    flex-direction: column;
    row-gap: 0.2cm;
}

.para1 {
    font-size: 50px;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width: fit-content;
}

.para1:hover {
    background: linear-gradient(to right, #a60643,
            #d05808, #e70fe7, #f6e765, rgb(162, 1, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.para2 {
    font-size: 20px;
    width: 70%;
}

.topic {
    border:1px solid rgb(195, 175, 237);
    padding:0.5cm;
    font-size: 20px;
    width:fit-content;
    border-radius: 0px 30px;
    margin:0;
    z-index: 1;
    background-color: rgb(211, 191, 243);
    box-shadow: rgba(56, 20, 58, 1) 0px 5px 15px;
}

.photo {
    padding: 0.5cm 1cm;
    margin: 0.5cm;
}

.p-photo {
    height: 13cm;
    width: 13cm;
    border-radius: 100%;
    padding: 0.5cm;
}

.aboutMe {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

}

.timeline{
    padding: 0.2cm;
margin:0.5cm;
}

.hr1{
    width:100%;
    /* border:0.1px solid rgb(0, 0, 0); */
    margin:0;

}

.left {
    display: flex;
    flex-direction: row;
    justify-content: left;
    margin-left: 1cm;
    margin-bottom:0;
}


