.chart{
padding:0.5cm 1cm;
border: 1px solid grey;
width: fit-content;
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
}

.progress{
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 0.5cm;
    padding:1cm;
    margin:0.5cm;
}