.ContactMe{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:1cm;
    padding:1cm;
}



.contact-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.5cm;
  row-gap: 0.5cm;
  padding: 0.5cm;
  margin: 3px;
  flex-wrap: wrap;

}

.contact{
  border: thin solid antiquewhite;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 12px;
  padding: 15px;
  height: fit-content;
  width:fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.2cm;
  border-radius: 15px;
}

.contact:hover{
  transform: scale(1.1);
  transition: 0.5s;

}
.contactImg{
  height: 1.5cm;
  width: 1.5cm;
  
}